import DisplayObject from './DisplayObject.js';
import {Button} from './Button.js';
import {renderer} from './Renderer.js';
import {pointer} from './input.js';

export let draggableSprites = [];
export let buttons = [];

export function updateDragAndDrop() {
  let canvas = renderer.canvas;
  let dragSprite = null,
    dragOffsetX = 0,
    dragOffsetY = 0;

  if (pointer.isDown) {
    if (dragSprite === null) {
      for (var i = draggableSprites.length - 1; i > -1; i--) {
        let sprite = draggableSprites[i];
        if (pointer.hitTestSprite(sprite) && sprite.draggable) {
          dragOffsetX = pointer.x - sprite.gx;
          dragOffsetY = pointer.y - sprite.gy;
          dragSprite = sprite;
          let children = sprite.parent.children;
          children.splice(children.indexOf(sprite), 1);
          children.push(sprite);
          break;
        }
      }
    } else {
      dragSprite.x = pointer.x - dragOffsetX;
      dragSprite.y = pointer.y - dragOffsetY;
    }
  }

  if (pointer.isUp) {
    dragSprite = null;
  }

  draggableSprites.some(sprite => {
    if (pointer.hitTestSprite(sprite) && sprite.draggable) {
      canvas.style.cursor = "pointer";
      return true;
    } else {
      canvas.style.cursor = "auto";
      return false;
    }
  });
}

function makeInteractive(o) {
  o.press = o.press || undefined;
  o.release = o.release || undefined;
  o.over = o.over || undefined;
  o.out = o.out || undefined;
  o.tap = o.tap || undefined;

  o.state = "up";
  o.action = "";
  o.pressed = false;
  o.hoverOver = false;

    //   update  preUpdate
  if(o.update)
    o.preUpdate = o.update;

  o.update = (dt) => {
    // update
    if(o.preUpdate)
      o.preUpdate(dt);
    
    let canvas = renderer.canvas;
    // console.log('interactive update');
    if (o.visible == false) return;
    let hit = pointer.hitTestSprite(o);

    if (pointer.isUp) {
      o.state = "up";
      if (o instanceof Button) o.gotoAndStop(0);
    }

    if (hit) {
      o.state = "over";
      if (o.frames && o.frames.length === 3 && o instanceof Button) {
        o.gotoAndStop(1);
      }

      if (pointer.isDown) {
        o.state = "down";
        if (o instanceof Button) {
          if (o.frames.length === 3) {
            o.gotoAndStop(2);
          } else {
            o.gotoAndStop(1);
          }
        }
      }
    }

    if (o.state === "down") {
      if (!o.pressed) {
        if (o.press) o.press();
        o.pressed = true;
        o.action = "pressed";
      }
    }

    if (o.state === "over") {
      if (o.pressed) {
        if (o.release) o.release();
        o.pressed = false;
        o.action = "released";
        if (pointer.tapped && o.tap) o.tap();
      }

      if (!o.hoverOver) {
        if (o.over) o.over();
        o.hoverOver = true;
      }
    }

    if (o.state === "up") {
      if (o.pressed) {
        if (o.release) o.release();
        o.pressed = false;
        o.action = "released";
      }

      if (o.hoverOver) {
        if (o.out) o.out();
        o.hoverOver = false;
      }
    }

    // 
    if (o.state === "over" || o.state === "down") {
        renderer.canvas.style.cursor = "pointer";
    }

  };
}

Object.defineProperties(DisplayObject.prototype, {
  interactive: {
    get() {
      return this._interactive;
    },
    set(value) {
      if (value === true) {
        makeInteractive(this);
        // buttons.push(this);
        this._interactive = true;
      }
      if (value === false) {
        // buttons.splice(buttons.indexOf(this), 1);
        this._interactive = false;
      }
    },
    enumerable: true,
    configurable: true
  },
  draggable: {
    get() {
      return this._draggable;
    },
    set(value) {
      if (value === true) {
        draggableSprites.push(this);
        this._draggable = true;
      }
      if (value === false) {
        draggableSprites.splice(draggableSprites.indexOf(this), 1);
      }
    },
    enumerable: true,
    configurable: true
  }
});
