import DisplayObject from './DisplayObject.js';
import {stage} from './Stage.js';

export class Rectangle extends DisplayObject {
  constructor(width = 32, height = 32,
              fillStyle = 'gray', strokeStyle = 'none',
              lineWidth = 0,
              x = 0, y = 0) {
    super();

    this.width = width;
    this.height = height;
    this.fillStyle = fillStyle;
    this.strokeStyle = strokeStyle;
    this.lineWidth = lineWidth;
    this.x = x;
    this.y = y;

    this.mask = false;
  }

  render(ctx) {
    ctx.strokeStyle = this.strokeStyle;
    ctx.lineWidth = this.lineWidth;
    ctx.fillStyle = this.fillStyle;
    ctx.beginPath();
    ctx.rect(
      -this.width * this.pivotX,
      -this.height * this.pivotY,
      this.width,
      this.height
    );
    if(this.strokeStyle !== 'none') ctx.stroke();
    if(this.fillStyle !== 'none') ctx.fill();
    if(this.mask && this.mask === true) ctx.clip();
  }
}

export class RoundedRectangle extends DisplayObject {
  constructor(width = 32, height = 32, radius = 30,
              fillStyle = 'gray', strokeStyle = 'none',
              lineWidth = 0,
              x = 0, y = 0) {
    super();

    this.width = width;
    this.height = height;
    this.radius = radius;
    this.fillStyle = fillStyle;
    this.strokeStyle = strokeStyle;
    this.lineWidth = lineWidth;
    this.x = x;
    this.y = y;

    this.mask = false;
  }

  render(ctx) {
    ctx.strokeStyle = this.strokeStyle;
    ctx.lineWidth = this.lineWidth;
    ctx.fillStyle = this.fillStyle;
    ctx.beginPath();

    let x = -this.width * this.pivotX;
    let y = -this.height * this.pivotY;
    let width = this.width;
    let height = this.height;
    let radius = this.radius;
    ctx.arc(x + radius, y + radius, radius, Math.PI, Math.PI * 3 / 2);   
    ctx.lineTo(width - radius + x, y);   
    ctx.arc(width - radius + x, radius + y, radius, Math.PI * 3 / 2, Math.PI * 2);   
    ctx.lineTo(width + x, height + y - radius);   
    ctx.arc(width - radius + x, height - radius + y, radius, 0, Math.PI * 1 / 2);   
    ctx.lineTo(radius + x, height +y);   
    ctx.arc(radius + x, height - radius + y, radius, Math.PI * 1 / 2, Math.PI);   

    if(this.strokeStyle !== 'none') ctx.stroke();
    if(this.fillStyle !== 'none') ctx.fill();
    if(this.mask && this.mask === true) ctx.clip();
  }
}

export function rectangle(width, height,
                          fillStyle, strokeStyle,
                          lineWidth, x, y) {
  let sprite = new Rectangle(width, height,
                             fillStyle, strokeStyle,
                             lineWidth, x, y);
  stage.addChild(sprite);
  return sprite;
}

export class Circle extends DisplayObject {
  constructor(diameter = 32,
              fillStyle = 'gray', strokeStyle = 'none',
              lineWidth = 0,
              x = 0, y = 0) {
    super();

    this.circular = true;

    this.diameter = diameter;
    this.fillStyle = fillStyle;
    this.strokeStyle = strokeStyle;
    this.lineWidth = lineWidth;
    this.x = x;
    this.y = y;

    this.mask = false;
  }

  render(ctx) {
    ctx.strokeStyle = this.strokeStyle;
    ctx.lineWidth = this.lineWidth;
    ctx.fillStyle = this.fillStyle;
    ctx.beginPath();
    ctx.arc(
      this.radius + (-this.diameter * this.pivotX),
      this.radius + (-this.diameter * this.pivotY),
      this.radius,
      0, 2 * Math.PI,
      false
    );
    if(this.strokeStyle !== 'none') ctx.stroke();
    if(this.fillStyle !== 'none') ctx.fill();
    if(this.mask && this.mask === true) ctx.clip();
  }
}

export function circle(diameter,
                       fillStyle, strokeStyle,
                       lineWidth,
                       x, y){
  let sprite = new Circle(diameter,
                          fillStyle, strokeStyle,
                          lineWidth,
                          x, y);
  stage.addChild(sprite);
  return sprite;
}

export class Line extends DisplayObject{
  constructor(
    strokeStyle = 'none',
    lineWidth = 0,
    ax = 0,
    ay = 0,
    bx = 32,
    by = 32
  ) {
    super();

    this.strokeStyle = strokeStyle;
    this.lineWidth = lineWidth;
    this.ax = ax;
    this.ay = ay;
    this.bx = bx;
    this.by = by;

    this.lineJoin = 'round';
  }

  render(ctx) {
    ctx.strokeStyle = this.strokeStyle;
    ctx.lineWidth = this.lineWidth;
    ctx.lineJoin = this.lineJoin;
    ctx.beginPath();
    ctx.moveTo(this.ax, this.ay);
    ctx.lineTo(this.bx, this.by);
    if(this.strokeStyle !== 'none') ctx.stroke();
  }
}

export function line(strokeStyle, lineWidth,
                     ax, ay,
                     bx, by) {
  let sprite = new Line(strokeStyle, lineWidth,
                        ax, ay,
                        bx, by);
  stage.addChild(sprite);
  return sprite;
}
