import common from './common.js';
import {makeCanvas} from './util.js';

const DEFAULT_RENDER_WIDTH = 640;
const DEFAULT_RENDER_HEIGHT = 480;

export let renderer = undefined;

export default class Renderer {
  constructor(width = DEFAULT_RENDER_WIDTH, height = DEFAULT_RENDER_HEIGHT, stage = undefined) {
    if(renderer) {
      console.error('renderer already instantiated');
      return renderer;
    }
    this.canvas = makeCanvas(width, height);
    document.body.appendChild(this.canvas);
    this.stage = stage;
    renderer = this;
    return renderer;
  }

  get backgroundColor() {
    return this.canvas.style.backgroundColor;
  }

  set backgroundColor(color){
    this.canvas.style.backgroundColor = color;
  }

  render() {
    let canvas = this.canvas;
    let ctx = canvas.ctx;

    ctx.clearRect(0, 0, canvas.width, canvas.height);

    if(this.stage){
      this.stage.children.forEach(sprite => {
        render.call(this, sprite);
      });
    }
  }
}

function render(sprite) {
  let canvas = this.canvas;
  let ctx = canvas.ctx;
  if (sprite.visible &&
      sprite.gx < canvas.width + sprite.width &&
      sprite.gx + sprite.width >= -sprite.width &&
      sprite.gy < canvas.height + sprite.height &&
      sprite.gy + sprite.height >= -sprite.height) {

    ctx.save();

    // // 
    ctx.translate(
      sprite.x + (sprite.width * sprite.pivotX),
      sprite.y + (sprite.height * sprite.pivotY)
      // sprite.x,
      // sprite.y
      // 0,0
    );
    ctx.rotate(sprite.rotation);
    ctx.scale(sprite.scaleX, sprite.scaleY);
    // ctx.setTransform(sprite.scaleX, 0, 0, sprite.scaleY, sprite.x, sprite.y);
    // ctx.setTransform(1, 0, 0, 1, sprite.x, sprite.y);

    // 
    ctx.globalAlpha = sprite.alpha * sprite.parent.alpha;

    // TODO:  ---  

    if (sprite.render) sprite.render(ctx);

    if (sprite.children && sprite.children.length > 0) {
      ctx.translate(-sprite.width * sprite.pivotX, -sprite.height * sprite.pivotY);
      // ctx.translate(-sprite.pivotX, -sprite.pivotY);
      // ctx.setTransform(1, 0, 0, 1, 0, 0);
      sprite.children.forEach(child => {
        render.call(this, child);
      });
    }

    ctx.restore();
  }
}
