import DisplayObject from './DisplayObject.js';
import {
  stage
} from './Stage.js';

export class Group extends DisplayObject {
  constructor(...sprites) {
    super();
    sprites.forEach(sprite => this.addChild(sprite));
  }

  addChild(sprite) {
    super.addChild(sprite);
    Group.calculateSize(this);
  }

  removeChild(sprite) {
    if (sprite.parent === this) {
      this.children.splice(this.children.indexOf(sprite), 1);
      Group.calculateSize(this);
    } else {
      throw new Error(`${sprite} is not a child of ${this}`);
    }
  }

  static calculateSize(group) {
    if (group.children.length > 0) {
      let newWidth = 0;
      let newHeight = 0;

      group.children.forEach(child => {
        if (child.x + child.width > newWidth)
          newWidth = child.x + child.width;
        if (child.y + child.height > newHeight)
          newHeight = child.y + child.height;
      });

      group.width = newWidth;
      group.height = newHeight;
    }
  }

}

export function group(...sprites) {
  let sprite = new Group(...sprites);
  stage.addChild(sprite);
  return sprite;
}

export function grid(columns = 0, rows = 0, cellWidth = 32, cellHeight = 32,
  centerCell = false, xOffset = 0, yOffset = 0,
  makeSprite = undefined,
  extra = undefined) {

  let container = group();

  let createGrid = () => {
    let length = columns * rows;

    for (let i = 0; i < length; i++) {
      let x = (i % columns) * cellWidth;
      let y = Math.floor(i / columns) * cellHeight;
      let sprite = makeSprite();

      if (!sprite)
        console.warn('grid makeSprite return: ' + sprite);

      container.addChild(sprite);

      if (!centerCell) {
        sprite.x = x + xOffset;
        sprite.y = y + yOffset;
      } else {
        sprite.x = x + (cellWidth / 2) -
          sprite.halfWidth + xOffset;
        sprite.y = y + (cellHeight / 2) -
          sprite.halfHeight + yOffset;
      }

      if (extra) extra(sprite);
    }
  };

  createGrid();

  return container;
}
