import {EventEmitter} from './EventEmitter.js';
import {makeCanvas} from './util.js';

export default class DisplayObject extends EventEmitter{
  constructor() {
    super();
    //  position and size
    this.x = 0;
    this.y = 0;
    this.lastX = 0;
    this.lastY = 0;
    this.width = 0;
    this.height = 0;

    // rotation alpha visible and scale properties
    this.rotation = 0;
    this.alpha = 1;
    this.visible = true;
    this.scaleX = 1;
    this.scaleY = 1;

    //  pivot let you set the sprite's axis of rotation
    this.pivotX = 0.5;
    this.pivotY = 0.5;

    // // 
    // this.vel = {x: 0, y: 0};
    // this.maxVel = {x:100,y:100};
    // this.accel = {x: 0, y: 0};
    // this.friction = {x: 0, y: 0};
    // this.gravityFactor = 1;

    //  
    this._layer = 0;
    this.children = [];
    this.parent = undefined;

    // image states and animation
    this.frames = [];
    this._currentFrame = 0;

    // 
    this._draggable = undefined;

    this._circular = false;

    this._interactive = false;
  }

  get gx() {
    if (this.parent)
      return this.x + this.parent.gx;
    else
      return this.x;
  }

  get gy() {
    if (this.parent)
      return this.y + this.parent.gy;
    else
      return this.y;
  }

  get layer() {
    return this._layer;
  }
  set layer(value) {
    this._layer = value;
    if (this.parent)
      this.parent.children.sort((a, b) => a._layer - b._layer);
  }

  addChild(sprite) {
    if (sprite.parent)
      sprite.parent.removeChild(sprite);
    sprite.parent = this;
    this.children.push(sprite);
  }

  removeChild(sprite) {
    if (this.hasChild(sprite)) {
      this.children.splice(this.children.indexOf(sprite), 1);
      sprite.parent = undefined;
    } else
      throw new Error(sprite + 'is not a child of' + this);
  }

  hasChild(sprite) {
    return (this.children.indexOf(sprite) !== -1);
  }

  get halfWidth() {
    return this.width >> 1;
  }

  get halfHeight() {
    return this.height >> 1;
  }

  // TODO:  pivot  0.5 
  get centerX() {
    return this.x + this.halfWidth;
  }

  get centerY() {
    return this.y + this.halfHeight;
  }
  get center() {
    return {
      x: this.centerX,
      y: this.centerY
    };
  }

  get gCenter() {
    if (this.parent)
      return {x:this.centerX + this.parent.gx,
              y:this.centerY + this.parent.gy};
    else
      return this.center;

  }

  get position() {
    return {
      x: this.x,
      y: this.y
    };
  }

  set postion(pos) {
    this.x = pos.x;
    this.y = pos.y;
  }

  setPosition(x, y) {
    this.x = x;
    this.y = y;
  }

  get scale() {
    return {x:this.scaleX,
            y:this.scaleY};
  }
  set scale(scale) {
    if(scale instanceof Object) {
    } else {
      this.scaleX = this.scaleY = scale;
    }
  }

  get localBounds() {
    return {
      x: 0,
      y: 0,
      width: this.width,
      height: this.height
    };
  }

  get globalBounds() {
    return {
      x: this.gx,
      y: this.gy,
      width: this.gx + this.width,
      height: this.gy + this.height
    };
  }

  get isEmpty() {
    if (this.children.length === 0)
      return true;
    else
      return false;
  }

  putCenter(b, offsetX = 0, offsetY = 0) {
    let a = this;
    b.x = (a.x + a.halfWidth - b.halfWidth) + offsetX;
    b.y = (a.y + a.halfHeight - b.halfHeight) + offsetY;
  }

  putTop(b, offsetX = 0, offsetY = 0) {
    let a = this;
    b.x = (a.x + a.halfWidth - b.halfWidth) + offsetX;
    b.y = (a.y - b.height) + offsetY;
  }

  putRight(b, offsetX = 0, offsetY = 0) {
    let a = this;
    b.x = (a.x + a.width) + offsetX;
    b.y = (a.y + a.halfHeight - b.halfHeight) + offsetY;
  }

  putBottom(b, offsetX = 0, offsetY = 0) {
    let a = this;
    b.x = (a.x + a.halfWidth - b.halfWidth) + offsetX;
    b.y = (a.y + a.height) + offsetY;
  }

  putLeft(b, offsetX = 0, offsetY = 0) {
    let a = this;
    b.x = (a.x - b.width) + offsetX;
    b.y = (a.y + a.halfHeight - b.halfHeight) + offsetY;
  }

  swapChildren(child1, child2) {
    let index1 = this.children.indexOf(child1);
    let index2 = this.children.indexOf(child2);
    if (index1 !== -1 && index2 !== -1) {
      this.children[index1] = child2;
      this.children[index2] = child1;
    } else {
      throw new Error(`Both objects must be a child of the caller ${this}`);
    }
  }

  add(...sprites) {
    sprites.forEach(sprite => this.addChild(sprite));
  }

  remove(...sprites) {
    sprites.forEach(sprite => {
      console.log('remove: ' + sprite.constructor.name);
      this.removeChild(sprite);
    });
  }

  get currentFrame() {
    return this._currentFrame;
  }

  get circular() {
    return this._circular;
  }

  set circular(value) {
    if (value === true && this._circular === false) {
      Object.defineProperties(this, {
        diameter: {
          get() {
            return this.width;
          },
          set(value) {
            this.width = this.height = value;
          },
          enumerable: true,
          configurable: true
        },
        radius: {
          get() {
            return this.halfWidth;
          },
          set(vlaue) {
            this.width = this.height = value << 1;
          },
          enumerable: true,
          configurable: true
        }
      });
      this._circular = true;
    }

    if (value === false && this._circular === true) {
      delete this.diameter;
      delete this.radius;
      this._circular = false;
    }
  }

}

let canvas =  makeCanvas();
if(canvas && canvas.ctx) {
  DisplayObject.hitTestCanvas = canvas;
  canvas.width = canvas.height = 1;
  // canvas.width = 512;
  // canvas.height = 256;
}

