import Tina from '../libs/tina/Tina.js';
import DisplayObject from '../libs/tina/DisplayObject.js';
import {Text} from '../libs/tina/Text.js';
export default function main() {

}
let lotNum = window.lotNum || [];
let speeds = [15,13,14,13,15]; //
let circle = 2;//

let qiuNum = 5;// 
let qiumen = [];

let count = 12; //
let num = []; //
//let numX = 35;//
let numH = 90;//
let totalH = numH * count;// 

let assetseot;
if(navigator.userAgent.split(";")[1].toLowerCase().indexOf("msie 10.0")=="-1"?false:true){
  assetseot = 'assets/impact.ttf'
}
else{
  assetseot = 'assets/impact.ttf'
}

let t = new Tina(580,96,game,[assetseot,'assets/qiu.png','assets/bai.png']);

class Qiuqiu extends DisplayObject{
  constructor() {
    super();
    this.speed = undefined;
    this.loopNum = undefined;
    this.showNum = undefined;
    this.numbers = [];

    //
    this.bg = new t.Sprite(t.assets['assets/qiu.png']);
    this.bai = new t.Sprite(t.assets['assets/bai.png']);
    this.bai.x = 16;
    this.bai.y = -55;
    this.bai2 = new t.Sprite(t.assets['assets/bai.png']);
    this.bai2.x = 16;
    this.bai2.y = 70;

    //
    for(let i=0;i<count;i++){
      let num;
      if( i <= 9 ){
        num =  new Text('0' + i,'35px impact','black');
      }
      else{
        num =  new Text(i,'35px impact','black');
      }
      num.width = 36;
      this.numbers[i] = num;
      this.addChild(num);
    }
    this.add(this.bai,this.bai2);
    this.addChild(this.bg);
    this.init();
  }

  // 
  init(){
    for(let i =0; i<count;++i){
      this.bg.putCenter(this.numbers[i]);
      this.numbers[i].y = numH * i;
      if(this.numbers[i].content == 11){
        this.numbers[i].x = 34;
      };
    }

  }

  // 
  play(speed,loopNum,showNum){
    this.speed = speed;
    this.loopNum = loopNum;
    this.showNum = showNum;
  }

  //
  update(){
    // speed   
    //
    if(this.speed > 0){
      for(let i = 0; i < count; i++){
        this.numbers[i].y += this.speed;
        var bottomY = 52;
        if(this.numbers[i].y >= bottomY){
          this.numbers[i].y = this.numbers[i].y - totalH;
          if(i === this.showNum)
            this.loopNum--;
        }
      }
      //
      if(this.loopNum === 0){
        let tmp;
        if(navigator.userAgent.indexOf("Chrome")!=-1){
          tmp = (21 -  this.numbers[this.showNum].y);
        }else if(navigator.userAgent.split(";")[1].toLowerCase().indexOf("msie 10.0")=="-1"?false:true) {
          tmp = (22 -  this.numbers[this.showNum].y);
        }
        else{
          tmp = (26 -  this.numbers[this.showNum].y);
        }
        if(tmp < this.speed){
          for(let i = 0; i < count; i++) {
            this.numbers[i].y += tmp;
          }
          this.speed = 0;
        }
      }
    }
  }


}

