function go(lotNum){
  if(lotNum == undefined){
    this.num = 1;
  }
  else{
    this.num = lotNum;
  }
  for(let i = 0; i< qiuNum;++i) {
    qiumen[i].play(speeds[i],circle,this.num[i]);
  }
}

function game() {
  //
  for(let i = 0; i< qiuNum;++i) {
    qiumen[i] = new Qiuqiu();
    t.stage.addChild(qiumen[i]);
    qiumen[i].x = i * 120;
  };
  //
  //window.kaisi();
  window.kaisi = go;
}

t.start();






