export class EventEmitter {
  constructor() {
    // console.info('EventEmitter  constructing');
    this._listeners = {};
  }

  on(event, listener) {
    if (typeof listener !== 'function')
      throw "Listener must be a function";

    this._listeners[event] = this._listeners[event] || [];
    this._listeners[event].push(listener);
  }

  emit(event, data) {
    let listener = this._listeners[event];
    if (listener) {
      for (var i = 0; i < listener.length; i++) {
        listener[i].call(this, data);
      }
    }
  }

  hasListener(event, listenr) {
    let listeners = this._listeners[event];
    for (var i = 0; i < listeners.length; i++) {
      if (!listenr || listenr == listeners[i]) {
        return true;
      }
    }
    return false;
  }

  removeListener(event, listener) {
    if (typeof listener !== 'function')
      throw "Listener must be a function";


    let listeners = this._listeners[event];
    let position = this._listeners[event].indexOf(listener);
    if (position != -1)
      this._listeners[event].splice(position, 1);
  }

  removeAllListeners(event) {
    if(event){
      this._listeners[event] = [];
    } else {
      this._listeners = {};
    }
  }
}
