function testHit(ctx) {
  try{
    return ctx.getImageData(0,0,1,1).data[3] > 1;
  } catch(e) {
    throw 'Error: when testHit';
  }
}

export let pointer = undefined;

export function makePointer(element, scale = 1) {
  if(pointer) {
    console.error('pointer already instantiated');
    return pointer; 
  }
  let _pointer = {
    element: element,
    scale: scale,
    _x: 0,
    _y: 0,
    tappedTime: 200,

    get x() {
      return this._x / this.scale;
    },
    get y() {
      return this._y / this.scale;
    },

    get centerX() {
      return this.x;
    },
    get centerY() {
      return this.y;
    },

    get position() {
      return {
        x: this.x,
        y: this.y
      };
    },

    isDown: false,
    isUp: true,
    tapped: false,

    downTime: 0,
    elapsedTime: 0,

    press: undefined,
    release: undefined,
    tap: undefined,

    dragSprite: null,
    dragOffsetX: 0,
    dragOffsetY: 0,

    moveHandler(event) {
      let element = event.target;
      this._x = (event.pageX - element.offsetLeft);
      this._y = (event.pageY - element.offsetTop);
      event.preventDefault();
    },

    touchmoveHandler(event) {
      let element = event.target;
      this._x = (event.targetTouches[0].pageX -element.offsetLeft);
      this._y = (event.targetTouches[0].pageY -element.offsetTop);
      event.preventDefault();
    },

    downHandler(event) {
      this.isDown = true;
      this.isUp = false;
      this.tapped = false;
      this.downTime = Date.now();
      if(this.press)
        this.press();
      event.preventDefault();
    },

    touchstartHandler(event) {
      let element = event.target;
      this._x = event.targetTouches[0].pageX - element.offsetLeft;
      this._y = event.targetTouches[0].pageY - element.offsetTop;

      this.isDown = true;
      this.isUp = false;
      this.isDown = false;
      this.downTime = Date.now();
      if(this.press) this.press();
      event.preventDefault();
    },

    upHandler(event) {
      this.elapsedTime = Math.abs(this.downTime - Date.now());
      if(this.elapsedTime <= this.tappedTime && this.tapped === false) {
        this.tapped = true;
        if(this.tap) this.tap();
      }
      this.isUp = true;
      this.isDown = false;
      if(this.release) this.release();
      event.preventDefault();
    },

    touchendHandler(event) {
      this.elapsedTime = Math.abs(this.downTime - Date.now());
      if(this.elapsedTime <= this.tappedTime && this.tapped === false) {
        this.tapped = true;
        if(this.tap) this.tap();
      }
      this.isUp = true;
      this.isDown = false;
      if(this.release) this.release();
      event.preventDefault();
    },

    // FIXME: sprite()(  )
    hitTestSpritePixel(sprite) {
      let ctx = DisplayObject.hitTestCanvas.ctx;
      // ctx.save();
      ctx.setTransform(1, 0, 0, 1, -this.x, -this.y);
      // ctx.transform(1,0,0,1,sprite.x,sprite.y);
      ctx.translate(
        sprite.gx + (sprite.width * sprite.pivotX),
        sprite.gy + (sprite.height * sprite.pivotY)
      );
      ctx.rotate(sprite.rotation);
      ctx.scale(sprite.scaleX, sprite.scaleY);

      sprite.render(ctx);
      let hit = testHit(ctx);
      ctx.setTransform(1,0,0,1,0,0);
      ctx.clearRect(0,0,1,1);
      // ctx.clearRect(0,0,512,256);

      // ctx.restore();
      if(hit) console.log('pixel hit');
      return hit;
    },

    // FIXME: sprite()(  )
    hitTestSpriteScaled(sprite) {
      let hit = false;
      if(!sprite.circular) {
        let center = sprite.gCenter;
        let halfWidht = (sprite.width >> 1) * sprite.scaleX;
        let halfHeight = (sprite.height >> 1) * sprite.scaleY;
        let left =  center.x - halfWidht,
            right = center.x + halfWidht,
            top = center.y - halfHeight,
            bottom = center.y + halfHeight;

        hit = this.x > left && this.x < right
          && this.y > top && this.y < bottom;
      } else {
        // FIXME: 
        let vx = this.x - (sprite.gx + sprite.radius),
        vy = this.y - (sprite.gy + sprite.radius),
        distance = Math.sqrt(vx * vx + vy * vy);
        hit = distance < sprite.radius;
      }
      // if(hit) console.log('scaled hit');
      return hit;
    },

    hitTestSpriteNormal(sprite) {
      let hit = false;
      if(!sprite.circular) {
        let left =  sprite.gx,
            right = sprite.gx + sprite.width,
            top = sprite.gy,
            bottom = sprite.gy + sprite.height;

        hit = this.x > left && this.x < right
          && this.y > top && this.y < bottom;
      } else {
        let vx = this.x - (sprite.gx + sprite.radius),
            vy = this.y - (sprite.gy + sprite.radius),
            distance = Math.sqrt(vx * vx + vy * vy);
        hit = distance < sprite.radius;
      }
      return hit;
    },

    // sprite(),pixel
    hitTestSprite(sprite) {
      if(sprite.rotation != 0) {
        return this.hitTestSpritePixel(sprite);
      } else if(sprite.scaleX != 1 || sprite.scaleY != 1){
        return this.hitTestSpriteScaled(sprite);
      } else {
        return this.hitTestSpriteNormal(sprite);
      }
    },

    updateDragAndDrop(draggableSprites) {
      if(this.isDown) {
        if(this.dragSprite === null) {
          for(let i = draggableSprites.length -1; i > -1; --i) {
            let sprite = draggableSprites[i];
            if(sprite.draggable && this.hitTestSprite(sprite)){
              this.dragOffsetX = this.x - sprite.gx;
              this.dragOffsetY = this.y - sprite.gy;
              this.dragSprite = sprite;
              let children = sprite.parent.children;
              children.splice(children.indexOf(sprite), 1);
              children.push(sprite);

              draggableSprites.splice(draggableSprites.indexOf(sprite),1);
              draggableSprites.push(sprite);

              break;
            }
          }
        } else {
          this.dragSprite.x = this.x - this.dragOffsetX;
          this.dragSprite.y = this.y - this.dragOffsetY;
        }
      }

      if(this.isUp)
        this.dragSprite = null;

      draggableSprites.some(sprite =>{
        if(sprite.draggable && this.hitTestSprite(sprite)) {
          this.element.style.cursor = 'pointer';
          return true;
        } else {
          this.element.style.cursor = 'auto';
          return false;
        }
      });
    }
  };

  element.addEventListener('mousemove', _pointer.moveHandler.bind(_pointer), false);
  element.addEventListener("mousedown", _pointer.downHandler.bind(_pointer), false);
  window.addEventListener("mouseup", _pointer.upHandler.bind(_pointer), false);

  element.addEventListener("touchmove", _pointer.touchmoveHandler.bind(_pointer), false);
  element.addEventListener("touchstart", _pointer.touchstartHandler.bind(_pointer), false);
  window.addEventListener("touchend", _pointer.touchendHandler.bind(_pointer), false);

  // Disable the default actions on the 'canvas'
  element.style.touchAction = "none";

  pointer = _pointer;
  return _pointer;
}

export function keyboard(keyCode) {
  let key = {};
  key.code = keyCode;
  key.isDown = false;
  key.isUp = true;
  key.press = undefined;
  key.release = undefined;

  key.downHandler = function(event) {
    if(event.keyCode === key.code){
      if(key.isUp && key.press) key.press();
      key.isDown = true;
      key.isUp = false;
    }
    event.preventDefault();
  };

  key.upHandler = function(event) {
    if(event.keyCode === key.code){
      if(key.isDown && key.release) key.release();
      key.isDown = false;
      key.isUp = true;
    }
    event.preventDefault();
  };

  window.addEventListener('keydown', key.downHandler.bind(key), false);
  window.addEventListener('keyup', key.upHandler.bind(key), false);

  return key;
}
