import common from './common.js';
//import {makeSound} from './sound.js';

export let assets = {
  toLoad: 0,
  loaded: 0,

  imageExtensions: ['png', 'jpg', 'gif'],
  fontExtensions: ['ttf', 'otf', 'ttc', 'woff'],
  jsonExtensions: ['json'],
  audioExtensions: ['mp3', 'ogg', 'wav', 'webm'],

  // TODO: 
  load: function(sources = []) {
    return new Promise(resolve => {
      let loadHandler = () => {
        this.loaded++;
        console.log(this.loaded);
        if (this.toLoad === this.loaded) {
          this.loaded = this.toLoad = 0;
          console.info('Assets finished loading');
          resolve();
        }
      };
      if (!sources || sources.length < 1) {
        console.error('no assets need to load');
        return;
      }
      console.info('Loading assets...');

      this.toLoad = sources.length;
      sources.forEach(source => {
        let extension = source.split('.').pop();
        // Load images
        if (this.imageExtensions.has(extension)) {
          this.loadImage(source, loadHandler);
        }
        // Load fonts 
        else if (this.fontExtensions.has(extension)) {
          this.loadFont(source, loadHandler);
        }
        // Load JSON files  
        else if (this.jsonExtensions.has(extension)) {
          this.loadJson(source, loadHandler);
        }
        // Load audio files  
        else if (this.audioExtensions.has(extension)) {
          this.loadSound(source, loadHandler);
        }
        // Display a message if a file type isn't recognized
        else {
          console.log("File type not recognized: " + source);
        }
      });
    });
  },

  loadImage: function(source, loadHandler) {
    let image = new Image();
    image.addEventListener('load', loadHandler, false);
    this[source] = image;
    image.src = source;
  },

  loadFont: function(source, loadHandler) {
    let fontFamily = source.split('/').pop().split('.')[0];
    let newStyle = $$new('style');
    let fontFace = "@font-face {font-family: '" + fontFamily +
      "'; src: url('" + source + "');}";
    newStyle.appendChild(document.createTextNode(fontFace));
    document.head.appendChild(newStyle);
    loadHandler();
  },

  loadJson: function(source, loadHandler) {
    let xhr = new XMLHttpRequest();
    xhr.open('GET', source, true);
    xhr.responseType = 'text';
    xhr.onload = event => {
      if (xhr.status === 200) {
        let file = JSON.parse(xhr.responseText);
        file.name = source;
        this[file.name] = file;
        if (file.frames)
          this.createTilesetFrames(file, source, loadHandler);
        else
          loadHandler();
      }
    };
    xhr.send();
  },

  createTilesetFrames: function(file, source, loadHandler) {
    let baseUrl = source.replace(/[^\/]*$/, '');
    let imageSource = baseUrl + file.meta.image;
    let image = new Image();
    let imageLoadHandler = () => {
      this[imageSource] = image;
      _.keys(file.frames).forEach(frame => {
        this[frame] = file.frames[frame];
        this[frame].source = image;
      });
      loadHandler();
    };
    image.addEventListener('load', imageLoadHandler, false);
    image.src = imageSource;
  },

  loadSound: function(source, loadHandler) {
    //let sound = makeSound(source,loadHandler);
    //sound.name = source;
    //this[sound.name] = sound;
  }
};
